
body{background-color: #fdf6e9}
*{
  font-family: 'Roboto';
}
a,p,button{
  font-size: 16px;
}  
.btn{
  background-color: #ca8c63;
  color: #ffffff;
  padding: 10px 25px;
  border: 3px solid #ca8c63;
  border-radius: 50px;
  font-weight: 600;
  margin: 0px 3px;
  box-shadow: 0px 0px 5px #a8a4a1;
  outline: none;
  transition: background-color 0.5s;
}
.btn:hover{
  background-color: #fceed6;
  color: #ca8c63;
  transition: background-color 0.5s;
}

/* ***********************
**   Menu Css
*********************** */
.SCB---menu{
  background-color: #fceed6;
  padding: 3px 0px;
  box-shadow: 0px 0.5px 3px #ca8c63;
}
.SCB---menu .btn:nth-child(1){
  margin-right: 15px;
}

.SCB-d-none-md{
  display: none;
}
@media only screen and (max-width: 600px) {
  .SCB---menu .btn{width: 100%;}
  .SCB-d-none-sm{
    display: none;
  }
  .SCB-d-none-md{
    display: block;
    margin: 10px 0px;
  }
}

/* ***********************
**   Dashboard Css
*********************** */
.SCB---dasbordLogo{
  padding: 3% 0px 4%;
}
.SCB---dasbordLogo img{
  width: 80%;
  max-width: 450px;
}


/* ***********************
**   Cards Css
*********************** */
.SCB---cards div{
  margin: 30px;
  background-color: #fceed6;
  padding: 35px 20px;
  border-radius: 50px;
  box-shadow: -3px 5px 14px -3px #ccbaad;
  text-align: center;
}
.SCB---cards div img{
  height: 130px;
  width: auto;
  margin-bottom: 15px;
}
.SCB---cards h3{
  margin: 5px 0px;
  font-weight: 700;
  font-size: 22px;
}
.SCB---card-larg div{
  background-image: url(../src/img/btcCuted.png);
  background-repeat: no-repeat;
  background-size: 250px auto;
  background-position: right;
  padding: 40px 5%;
  text-align: left;
}
@media only screen and (max-width: 600px) {
  .SCB---card-larg div{
    text-align: center;
    background-image: url(../src/img/btccoin.png);
    background-size: 100px auto;
    background-position: right bottom;
  }
  .SCB---cards div img{
    height: auto;
    width: 100%;
  }
  .SCB---cards div{
    margin: 20px 10px;
  }
}